.component40 {
    background: linear-gradient(
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 1)
      ),
      linear-gradient(
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 1)
      ),
      linear-gradient(
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 1)
      ),
      rgba(255, 255, 255, 1);
    border-radius: 10px;
    padding: 15px 34px 35px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .flexWrapperOne {
    width: 238px;
    height: 187px;
    background-size: 100% 100%;
    background-position: center;
    background: url("https://static.overlay-tech.com/assets/8dfdfede-7089-421e-bc9f-4c31131b07f0.png"),
      url("https://static.overlay-tech.com/assets/ff4a888e-2194-4bfc-903a-c8c92c5f7f4d.png"),
      url("https://static.overlay-tech.com/assets/7cbc8762-d889-42d7-b1c2-9fdc39305881.png"),
      url("https://static.overlay-tech.com/assets/9c0dd6a1-06be-49e2-92a8-c6d66d5fc711.png");
    margin-bottom: 25px;
    border-radius: 10px;
  }
  .flexWrapperTwo {
    position: relative;
    &:not(:last-of-type) {
      margin-bottom: 14px;
    }
  }
  .organization {
    width: 100%;
    height: 100%;
    color: rgba(0, 0, 0, 1);
    position: relative;
    @include lato-20-regular;
  }
  .organizationTwo {
    width: 100%;
    height: 100%;
    color: rgba(0, 0, 0, 1);
    position: absolute;
    left: 0;
    top: 0;
    @include lato-20-regular;
  }
  .thisIsADemoOrganizationInCodelabz {
    max-width: 267px;
    width: 100%;
    height: 100%;
    color: rgba(112, 112, 112, 1);
    text-align: center;
    position: relative;
    @include lato-16-regular;
  }
  .thisIsADemoOrganizationInCodelabzTwo {
    max-width: 267px;
    width: 100%;
    height: 100%;
    color: rgba(112, 112, 112, 1);
    text-align: center;
    position: absolute;
    left: 0;
    top: 0;
    @include lato-16-regular;
  }